import Vue from 'vue';
import Vuex from 'vuex';
import { Toast } from 'vant';
import * as API from '../api/index';

Vue.use(Vuex);
Vue.use(Toast);

export default new Vuex.Store({
  state: {
    sidebar: [],
    show: false, // 是否展示内容
    goodsList: [], // 商品列表
    size: 7,
    sortType: 'all',
    total: 0, // 商品总数
    counterMap: {},
  },
  mutations: {
    saveLocalStorage(state, { id, value }) {
      if (state.counterMap[id]) {
        if ((state.counterMap[id] === 1 && value === -1) || value === -Infinity) {
          Vue.delete(state.counterMap, id);
        } else {
          Vue.set(state.counterMap, id, state.counterMap[id] + value);
        }
      } else {
        Vue.set(state.counterMap, id, 1);
      }
      // console.log(state.counterMap);
      localStorage.setItem('goods', JSON.stringify(state.counterMap));
    },
    setCounterMap(state, map) {
      state.counterMap = map;
    },
    setSidebar(state, list) {
      state.sidebar = list;
    },
    setShow(state, bool) {
      state.show = bool;
    },
    setGoodsList(state, list) {
      state.goodsList = state.goodsList.concat(list);
    },
    resetGoodsList(state) {
      // 重置数据
      state.goodsList = [];
    },
    setType(state, type) {
      state.type = type;
    },
    setTotal(state, total) {
      state.total = total;
    },
  },
  actions: {
    async setSidebar(ctx, type) {
      ctx.commit('setShow', false);
      let sideList = await API.getsidebar(type);
      sideList = sideList.data;
      // console.log(sideList);
      ctx.commit('setSidebar', sideList);
      ctx.commit('resetGoodsList');
      ctx.commit('setShow', true);
    },
    async setGoodsList(ctx, params) {
      const { page } = params;

      let type = '';
      if (params.type) {
        type = params.type;
        // if (type === ctx.state.type) {
        //   ctx.commit('resetGoodsList');
        // }
        ctx.commit('setType', type);
      } else {
        type = ctx.state.type;
      }
      const size = params.size || ctx.state.size;
      const sort = params.sort || ctx.state.sortType;

      const goodsList = await API.getGoodsList(type, page, size, sort);
      // console.log(goodsList);
      ctx.commit('setGoodsList', goodsList.data.list);
      ctx.commit('setTotal', goodsList.data.total);
      // console.log(goodsList);
    },
  },
  modules: {
  },
});
