// 底部标签栏组件
<template>
  <van-tabbar v-model="active" route border fixed>
    <van-tabbar-item icon="home-o" to="/home">首页</van-tabbar-item>
    <van-tabbar-item icon="apps-o" to="/classify">分类</van-tabbar-item>
    <van-tabbar-item
      icon="shopping-cart-o"
      to="/shopping"
      :badge="badge"
      id="shop-car"
      >购物车</van-tabbar-item
    >
    <van-tabbar-item icon="user-circle-o" to="/home">我的</van-tabbar-item>
  </van-tabbar>
</template>

<script>
import { mapState } from 'vuex';

export default {
  data() {
    return {
      active: 0,
    };
  },
  computed: {
    ...mapState(['counterMap']),
    badge() {
      let count = Object.values(this.counterMap);
      // console.log(count);
      if (count.length === 0) {
        return 0;
      }
      count = count.reduce((preNum, curNum) => preNum + curNum);
      // console.log(count);
      return count;
    },
  },
};
</script>

