const axios = require('axios');

const instance = axios.create({
  baseURL: 'https://mallapi.duyiedu.com/goods',
  params: {
    appkey: 'loveEDG_1670229509744',
  },
});

// 获取二级导航
const getsidebar = async (params) => {
  const res = await instance.get('/getsidebar', {
    params,
  });
  return res;
};

// 获取商品列表
const getGoodsList = async (type, page, size, sort) => {
  const res = await instance.get('/getGoodsList', {
    params: {
      type,
      page,
      size,
      sort,
    },
  });
  return res;
};

// 模糊搜索商品列表
const likeSearch = async (likeValue) => {
  const res = await instance.get('/likeSearch', {
    params: {
      likeValue,
    },
  });
  return res;
};

// 搜索商品列表
const search = async (type, page, size) => {
  const res = await instance.get('/search', {
    params: {
      type,
      page,
      size,
    },
  });
  return res;
};

// 根据商品列表进行搜索
const getGoodsByIds = async (value) => {
  const res = await instance.get('/getGoodsByIds', {
    params: {
      value,
    },
  });
  return res;
};

export {
  getsidebar,
  getGoodsList,
  likeSearch,
  search,
  getGoodsByIds,
};
