import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../views/Home.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect: '/classify',
    meta: { level: 1 },
    children: [{
      path: 'classify',
      name: 'Classify',
      meta: { level: 1 },
      component: () => import('../views/classify.vue'),
    }, {
      path: 'shopping',
      name: 'Shopping',
      meta: { level: 1 },
      component: () => import('../views/shopping.vue'),
    }],
  },
  {
    path: '/self',
    name: 'Self',
    component: () => import('../views/self.vue'),
  },
  // {
  //   path: '/classify',
  //   name: 'Classify',
  //   component: () => import('../views/classify.vue'),
  // },
  // {
  //   path: '/shopping',
  //   name: 'Shopping',
  //   component: () => import('../views/shopping.vue'),
  // },
  {
    path: '/search',
    meta: { level: 2 },
    name: 'Search',
    component: () => import('../views/search.vue'),
  },
  {
    path: '*',
    redirect: '/classify',
  },
];

const router = new VueRouter({
  routes,
});

export default router;
